<script>
export default {
  name: 'app',
  mounted () {
    this.loadData()
  },
  methods: {
    loadData () {
      let me = this
      this.$http.post('/user/queryuserinfo', {}, res => {
        if (res.success) {
          $.extend(me.form, res.data)
        }
      })
      let enterYearData = []
      let nowYear = new Date().getFullYear()
      for (let i = 0; i < 10; i++) {
        enterYearData.push(nowYear - i)
      }
      this.enterYearData = enterYearData
    },
    changeEnterYear () {
      let me = this
      this.$http.post('/user/queryclass', {
        userEnterYear: this.form.userEnterYear
      }, res => {
        if (res.success) {
          me.classData = res.data
        }
      })
    },
    save (formName) {
      let me = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          me.$http.post('/user/modifybasicinfo', me.form, res => {
            if (res.success) {
              me.$message('保存成功')
            }
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    }
  },
  data () {
    return {
      avatar: 'https://gss0.bdstatic.com/-4o3dSag_xI4khGkpoWK1HF6hhy/baike/c0%3Dbaike92%2C5%2C5%2C92%2C30/sign=62d46c39067b020818c437b303b099b6/d4628535e5dde7119c3d076aabefce1b9c1661ba.jpg',
      classData: [],
      enterYearData: [],
      form: {
        userNickname: '',
        userSex: '男',
        userEmail: '',
        userRealname: '',
        userStudentNumber: '',
        schoolName: '',
        facultyName: '',
        majorName: '',
        userClassId: '',
        userEnterYear: ''
      },
      rules: {
        userRealname: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ],
        userSex: [
          { required: true, message: '请选择性别', trigger: 'blur' }
        ],
        userEmail: [
          { type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur' }
        ],
        username: [
          { required: true, message: '请输入姓名', trigger: 'blur' }
        ],
        userClassId: [
          { required: true, message: '请选择班级', trigger: 'blur' }
        ]
      }
    }
  }
}
</script>

